import React from "react";

class MemeGenerator extends React.Component {
  constructor() {
    super();
    this.state = {
      topText: "",
      bottomText: "",
      activeMeme:
        "https://imgflip.com/s/meme/Shut-Up-And-Take-My-Money-Fry.jpg",
      allMemes: [],
    };
    this.handleTextChange = this.handleTextChange.bind(this);
    this.handleRandomImg = this.handleRandomImg.bind(this);
    this.handleImgChange = this.handleImgChange.bind(this);
    this.handleGeneration = this.handleGeneration.bind(this);
  }

  componentDidMount() {
    fetch("https://api.imgflip.com/get_memes")
      .then((resp) => resp.json())
      .then((resp) => {
        const { memes } = resp.data;
        console.log(memes[8]);
        this.setState({ allMemes: memes });
      });
  }
  handleTextChange(event) {
    const { name, value, type } = event.target;
    type === "button"
      ? this.setState({
          topText: "",
          bottomText: "",
        })
      : this.setState({ [name]: value });
  }

  handleRandomImg(event) {
    event.preventDefault();
    const randNum = Math.floor(Math.random() * this.state.allMemes.length);
    this.setState({ activeMeme: this.state.allMemes[randNum].url });
  }

  handleImgChange(event) {
    this.setState({ activeMeme: URL.createObjectURL(event.target.files[0]) });
  }

  handleGeneration(event) {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  }

  render() {
    const { topText, bottomText, activeMeme } = this.state;
    return (
      <div>
        <form className="form">
          <div className="form__inputs">
            <input
              type="text"
              name="topText"
              value={topText}
              autocomplete="off"
              placeholder="Top Text"
              onChange={this.handleTextChange}
            />
            <br />
            <input
              type="text"
              name="bottomText"
              placeholder="Bottom Text"
              autocomplete="off"
              value={bottomText}
              onChange={this.handleTextChange}
            />
            <button type="button" onClick={this.handleTextChange}>
              clear
            </button>
          </div>
          <div className="form__btns">
            <button onClick={this.handleRandomImg}>Load Random image</button>
            <label htmlFor="fileInput">
              Open Local Image
              <input
                id="fileInput"
                name="fileInput"
                type="file"
                accept=".jpg, .jpeg, .png"
                onChange={this.handleImgChange}
                hidden
              />
            </label>
          </div>
        </form>
        <div className="content">
          <img src={activeMeme} alt="" />
          <h1>{topText}</h1>
          <h2>{bottomText}</h2>
        </div>
      </div>
    );
  }
}

export default MemeGenerator;
